import Vue from 'vue'
import Router from 'vue-router'

const _import = process.env.NODE_ENV == 'development' ? file => require('../view/' + file + '.vue').default : file => () => import('../view/' + file + '.vue')

Vue.use(Router)

const routes = [
    {
        path: '*',
        redirect: '/index'
    },
    {
        path: '/',
        redirect: '/index'
    },
    {
        path: '/login',
        component: _import('Login'),
        meta: {
            title: '登录'
        }
    },
    {
        path: '/reset',
        component: _import('Reset'),
        meta: {
            title: '找回密码'
        }
    },
    {
        path: '/',
        component: _import('Layout'),
        children: [{
            path: 'index',
            component: _import('Index'),
            meta: {
                title: '商城首页'
            }
        },
        {
            path: 'goods',
            component: _import('Goods'),
            meta: {
                title: '商品详情'
            }
        },
        {
            path: 'cart',
            component: _import('Cart'),
            meta: {
                title: '购物车'
            }
        },
        {
            path: 'orderDetail',
            component: _import('OrderDetail'),
            meta: {
                title: '订单详情页'
            }
        },
        {
            path: 'user',
            component: _import('User'),
            meta: {
                title: '个人中心',
                authorization:true
            }
        },
        {
            path: 'orderList',
            component: _import('OrderList'),
            meta: {
                title: '订单列表',
                authorization:true
            }
        },
        {
            path: 'pointsList',
            component: _import('PointsList'),
            meta: {
                title: '积分列表',
                authorization:true
            }
        },
        ]
    }

];
const router = new Router({ routes });

const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
    return routerPush.call(this, location).catch(function(error){
        return error
    })
}

