  methods:{
    getPar(str){
      const par = this[str] || sessionStorage.getItem(str)
      if(par){
        this.$store.commit("SET_"+str.toUpperCase(), par);
        sessionStorage.setItem(str, par);
      }
      return par
    },

    async mallInfo(){
      const query = this.$gql`
        query mall($mallId: String!) {
          mall {
            info(
              mallId: $mallId
            ) {
                description
                ico
                logo
                mallId
                mallName
                orgId
                title
            }
          }
        }
      `;
      const result = await this.$apollo.query({
        query: query,
        variables: {
          mallId: this.mallId
        }
      });
      let {
        data:{
          mall:{
            info
          }
        }
      } = result;
      console.log('app---',info)
      this.$store.commit("SET_ORGID", info.orgId);
    },

