import "babel-polyfill"
import Vue from 'vue'
import App from './App.vue'
import { Button } from 'vant'
import store from './store/index'
import router from './router'
import api from './api'
import util from './util'
import fetch from 'node-fetch'
import { ApolloClient } from 'apollo-client'
import { createHttpLink } from 'apollo-link-http'
import { InMemoryCache } from 'apollo-cache-inmemory'
import { ApolloLink } from 'apollo-link'
import VueApollo from 'vue-apollo'
import gql from "graphql-tag";
import Es6Promise from 'es6-promise'
Es6Promise.polyfill();
// Vue.config.productionTip = false
Vue.config.devtools = true;
// HTTP connection to the API
const httpLink = createHttpLink({
  // You should use an absolute URL here
  uri: 'http://gsd.umq.cn/api/graphql',
  fetch:fetch
})

const middlewareLink = new ApolloLink((operation, forward) => {
  operation.setContext({
    // headers: {
    //   'acces': "120000000"
    // },
    body: {
      requestId: '12345678'
    }
  })
  return forward(operation).map(response => {
    console.log('response----',response)
    if(response.errors){
      let message = response.errors[0].message
      alert(message)
    }
    return response
  })
})

const authLink = middlewareLink.concat(httpLink)
// Cache implementation
const cache = new InMemoryCache()

// Create the apollo client
const apolloClient = new ApolloClient({
  link: authLink,
  cache,
})


Vue.prototype.$http = api
Vue.prototype.$util = util
Vue.prototype.$gql = gql
Vue.use(Button)
Vue.use(VueApollo)

const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
})

new Vue({
  router,
  store,
  apolloProvider,
  render: h => h(App),
}).$mount('#app')


router.beforeEach((to, from, next) => {
  let isLogin = store.state.token// 是否登录
  let authorization = to.meta.authorization
  if(authorization){
    if(isLogin){
      next()
    }else{
      router.push({ path: '/login', query:{mallId:store.state.mallId}})
    }
  }
  next()
})